import React, { useEffect } from 'react';
import { apiUrl, MediaUpload } from '../../constants/api';
import Resizer from "react-image-file-resizer";

export default function SingleMediaUpload(props) {
  const {
    ImageUrl,
    setImageUrl,
    MediaUploadType,
    setMediaUploadType,
  } = props;
  const fileRefSingle = React.createRef();
  const [Loading, setLoading] = React.useState(false)

  const MediaUploadSingleCall = (file, i) => {
    if (!file) return;
    var fileInput = "";
    if (file) {
      fileInput = file.type;
    }
    console.log("2", file)
    setMediaUploadType(file.type.split("/")[0])
    if (fileInput.includes("image")) {
      try {
        Resizer.imageFileResizer(
          file,
          1000,
          1000,
          "JPEG",
          40,
          0,
          (uri) => {
            console.log("2", uri)
            // this.setState({ newImage: uri });
            MediaUpload(uri)
              .then(res => res.json())
              .then(data => {
                setImageUrl(data.source_url)
                setTimeout(() => {
                  setLoading(false)
                }, 1000);
              })
              .catch(err => { console.log("error", err.message); })
              .finally(() => { })
          },
          "file",
          200,
          200
        );
      } catch (err) {
        console.log(err);
      }
    }
    else {
      MediaUpload(file)
        .then(res => res.json())
        .then(data => {
          setImageUrl(data.source_url)
          setTimeout(() => {
            setLoading(false)
          }, 1000);
        })
        .catch(err => { console.log("error", err.message); })
        .finally(() => { })
    }

  }

  return (
    <div>
      {Loading &&
        <div className="spinner-border"></div>
      }
      <div className="col-lg-6 col-md-6">
        <div className="row">

          {ImageUrl !== "" ?
            <div className="col-3 my-2">
              <div className=" ">

                {MediaUploadType == "image" &&
                  <button type="button" onClick={() => { fileRefSingle.current.click() }}
                    className="bg-transparent border-0 p-1 "
                  >
                    <img src={`${ImageUrl}`} className={" border border-dark"}
                      height="100px"
                    />
                  </button>
                }
                {MediaUploadType == "audio" &&
                  <audio controls>
                    {/* <source src={`${ImageUrl}`} type="audio/ogg" /> */}
                    <source src={`${ImageUrl}`} type="audio/mpeg"/>
                    <source src={`${ImageUrl}`} type="audio/mp3"/>
                    <source src={`${ImageUrl}`} type="audio/wav"/>
                    <source src={`${ImageUrl}`} type="audio/ogg"/>
                    Your browser does not support the audio element.
                  </audio>
                }
                {MediaUploadType == "video" &&
                  <video width="400" controls>
                    <source src={`${ImageUrl}`} type="video/mp4"
                      style={{ height: "150px", width: "200px" }}
                    />
                  </video>
                }
                {MediaUploadType == "youtube" &&
                  <iframe width="400" height="150" src={`https://www.youtube.com/embed/${ImageUrl}?autoplay=1`} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                }
              </div>
              <button onClick={() => {
                setImageUrl("")
              }}
                className="bg-transparent border-0 p-1 "
              ><u>Clear</u></button>
            </div>
            :
            <div className="col-3">
              <button type="button"
                onClick={() => {
                  fileRefSingle.current.click()
                }}
                className="bg-transparent border-0 p-1 "
              >
                <div className="border border-dark m-1 p-4 font-size-12">
                  <i className="fa fa-camera font-size-18"></i>
                  <div>Add</div>
                </div>
              </button>
            </div>
          }
        </div>
      </div>
      <div style={{ display: 'none' }}>
        <input type="file" accept="*/*" name="image" id="file" ref={fileRefSingle}
          onChange={(event) => {
            if (event.target.files.length !== 0) {
              setLoading(true)
              MediaUploadSingleCall(event.target.files[0])
            }
          }}
        />
      </div>

    </div>
  )
}